import React, { memo } from 'react';
import { Grid } from '@material-ui/core';
import PropTypes from 'prop-types';

import {
  FormDateField
} from '../../../FormFields';
import Loading from '../../../Loading';

const ExportTicketsCsvForm = ({
  setFormData, isLoading,
  formData: {
    start_date, end_date, validationErrors: errors
  }
}) => {
  if (isLoading) return <Loading />;

  return (
    <Grid container direction="row" spacing={1}>
      <Grid item xs={12} sm={6}>
        <FormDateField
          errors={errors.start_date}
          label="Data de incio"
          disabled={isLoading}
          value={start_date}
          onChangeValue={newVal => setFormData({ start_date: newVal })}
          disableFuture
        />
      </Grid>
      <Grid item xs={12} sm={6}>
        <FormDateField
          errors={errors.end_date}
          label="Data final"
          disabled={isLoading}
          value={end_date}
          onChangeValue={newVal => setFormData({ end_date: newVal })}
          disableFuture
        />
      </Grid>
    </Grid>
  );
};

ExportTicketsCsvForm.propTypes = {
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  isLoading: PropTypes.bool
};

export default memo(ExportTicketsCsvForm);
